        eng_name: "Xingtian Temple Station",
        name: "⾏天宮站",
        description: `行天宮是大臺北地區的信仰重鎮，主神為關聖帝君(恩主公)，許多民眾皆會來此敬拜恩主公，又稱恩主公廟、臺北關帝廟或臺北武廟，是臺灣知名的關帝廟。
台灣的廟宇賦予著傳統藝術及台灣民間習俗文化，而知名的關帝廟不只供信仰的民眾前來敬拜，也成了不同信仰及外國旅客認識台灣文化的重要景點。`,
