        demo_title: `行天宮站`,
        demo_author: `楊尚峰`,
        demo_author_description: `同時是一名外銷業務人員及遊戲配樂師，自2015年起自學音樂製作，參與過各式音效設計、音樂編曲案。擅長氣勢宏偉壯闊的音樂創作。`,
        demo_description: `行天宮算是台北人的信仰中心之一；看著在該站下車的人們，踩著信仰的腳步，往著行天宮走去，向上天祈求著家人健康、平安。
        作品使用了中國傳統的樂器~笙來作為前半段的襯底，並用古箏來搭配主旋律，中間一小段的鈴聲，如叮叮叮…就如同誦經師們正在用經文穩定人心。
        後半段則是採用梆笛作為主旋律，搭配了莊嚴的大鼓，作為基礎節拍，而前段本來作為主旋律的古箏則變成伴奏，表現出行天宮的莊嚴及傳統信仰。`,
        demo: `/static/audio/第三期站體音檔/行天宮站-楊尚峰 MIX_02.01_01.mp3`
