        eng_name: "Daqiaotou Station",
        name: "⼤橋頭站",
        description: `為米食文化發源處的大橋頭一帶，早期又稱作草埔仔、四坎仔，由於鄰近淡水河，地勢平坦、土壤肥沃，因此自古盛產稻米，居民便用此研發出各項米食產品。
而迪化街商圈則為台灣南北貨、茶葉、中藥和布匹的批發貨集散地，更是台灣農曆新年採購必去的年貨大街。`,
